<template>
	<view class="body">
		<!-- 头部模块 -->
		<view class="head">
			<view class="word" @click="toNewWord">
				<img src="../../static/langStatic/wordBook.png">
				<text>单词本</text>
			</view>
			<view class="head_title">Text4，2008</view>
			<view class="difficulty">
				<view>难度</view>
				<u-icon name="star-fill" color="#ffd75c" size="28rpx" v-for="item in difficulty"></u-icon>
				<u-icon name="star-fill" color="#e3e3e3" size="28rpx" v-for="item in 3-difficulty"></u-icon>
			</view>
		</view>
		<!-- 句子 -->
		<view class="sentence" v-html="data.subject"></view>
		<!-- 图片 -->
		<image v-if="data.img" class="picture" :src="data.img" @click="openImg"></image>
		<!-- 单元 -->
		<view class="title">
			<view class="number">{{data.index}}</view>
			<view class="text">{{data.title}}</view>
		</view>
		<!-- 单选类型 -->
		<view v-if="data.type=='single_choice'" class="single_choice">
			<!-- 答案 -->
			<view class="answerList" v-if="isSee==false">
				<view class="answer" v-for="(item,index) in data.option" :key="index" @click="confirm(index)">
					<view v-if="index==0" :class="{serial:true, checked:item.isChecd} ">A</view>
					<view v-else-if="index==1" :class="{serial:true, checked:item.isChecd} ">B</view>
					<view v-else-if="index==2" :class="{serial:true, checked:item.isChecd} ">C</view>
					<view v-else-if="index==3" :class="{serial:true, checked:item.isChecd} ">D</view>
					<view v-else-if="index==4" :class="{serial:true, checked:item.isChecd} ">E</view>
					<view v-else-if="index==5" :class="{serial:true, checked:item.isChecd} ">F</view>
					<view v-else-if="index==6" :class="{serial:true, checked:item.isChecd} ">G</view>
					<view v-else-if="index==7" :class="{serial:true, checked:item.isChecd} ">H</view>
					<view class="text">{{item.option}}</view>
				</view>
				<button @click="seeResolution">查看解析</button>
			</view>
			<!-- 答案 -->
			<view class="answerList" v-else>
				<view class="answer" v-for="(item,index) in data.option_answer" :key="index">
					<view v-if="index==0" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">A</view>
					<view v-else-if="index==1" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">B</view>
					<view v-else-if="index==2" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">C</view>
					<view v-else-if="index==3" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">D</view>
					<view v-else-if="index==4" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">E</view>
					<view v-else-if="index==5" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">F</view>
					<view v-else-if="index==6" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">G</view>
					<view v-else-if="index==7" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">H</view>
					<view class="text">{{item.option}}</view>
				</view>
				<button>查看解析</button>
			</view>
		</view>
		<!-- 多选类型 -->
		<view v-if="data.type=='multiple_choice'" class="multiple_choice">
			<!-- 答案 -->
			<view class="answerList" v-if="isSee==false">
				<view class="answer" v-for="(item,index) in data.option" :key="index" @click="confirms(index)">
					<view v-if="index==0" :class="{serial:true, checked:item.isChecd} ">A</view>
					<view v-else-if="index==1" :class="{serial:true, checked:item.isChecd} ">B</view>
					<view v-else-if="index==2" :class="{serial:true, checked:item.isChecd} ">C</view>
					<view v-else-if="index==3" :class="{serial:true, checked:item.isChecd} ">D</view>
					<view v-else-if="index==4" :class="{serial:true, checked:item.isChecd} ">E</view>
					<view v-else-if="index==5" :class="{serial:true, checked:item.isChecd} ">F</view>
					<view v-else-if="index==6" :class="{serial:true, checked:item.isChecd} ">G</view>
					<view v-else-if="index==7" :class="{serial:true, checked:item.isChecd} ">H</view>
					<view class="text">{{item.option}}</view>
				</view>
				<button @click="seeResolution">查看解析</button>
			</view>
			<!-- 答案 -->
			<view class="answerList" v-else>
				<view class="answer" v-for="(item,index) in data.option_answer" :key="index">
					<view v-if="index==0" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">A</view>
					<view v-else-if="index==1" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">B</view>
					<view v-else-if="index==2" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">C</view>
					<view v-else-if="index==3" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">D</view>
					<view v-else-if="index==4" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">E</view>
					<view v-else-if="index==5" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">F</view>
					<view v-else-if="index==6" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">G</view>
					<view v-else-if="index==7" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">H</view>
					<view class="text">{{item.option}}</view>
				</view>
				<button>查看解析</button>
			</view>
		</view>
		<!-- 填空类型 -->
		<view v-else-if="data.type=='completion'" class="completion">
			<!-- 答案 -->
			<view v-if="isSee==false" class="answer">
				<view class="cell sortInput" v-for="(item,index) in data.option" :key="index">
					<view class="key">{{item.key}}: </view>
					<view class="value">
						<input class="inputs" type="text" v-model="result[index]" />
					</view>
				</view>
				<button @click="seeResolution">查看解析</button>
			</view>
			<!-- 答案 -->
			<view v-else class="answer">
				<view class="cell sortInput" v-for="(item,index) in data.option_answer" :key="index">
					<view class="key">{{item.key}}: </view>
					<view class="value">
						<input v-if="item.answer==item.value" class="inputs trueAnswers" type="text" v-model="item.answer" :disabled="true"/>
						<input v-if="item.flag=='false'" class="inputs falseAnswer" type="text" v-model="item.answer" :disabled="true"/>
						<view v-if="item.flag=='false'" class="input trueAnswer">{{item.value}}</view>
					</view>
					<!-- <input v-if="result[index]!=item.value" class="inputs trueAnswer" type="text" v-model="item.value" :disabled="true"/> -->
				</view>
				<button>查看解析</button>
			</view>
		</view>
		<!-- 翻译类型 -->
		<view v-else-if="data.type=='translate'" class="translate">
			<!-- 答案 -->
			<view v-if="isSee==false" class="answer">
				<textarea name="" id="" v-model="text" cols="30" rows="10"></textarea>
				<button @click="seeResolution">查看解析</button>
			</view>
			<!-- 答案 -->
			<view v-else class="answer">
				<textarea v-if="data.option_answer" name="" id="" v-model="data.option_answer.answer" cols="30" rows="10" :disabled="true"></textarea>
				<textarea v-else name="" id="" v-model="text" cols="30" rows="10" :disabled="true"></textarea>
				<button>查看解析</button>
			</view>
		</view>
		
		<view v-if="data.type== single_choice || translate" class="tips">注: 输入内容中有多余空格、错误标点符号、拼写错误等皆不正确。</view>
		<!-- 答案解析 -->
		<view v-if="data.analysis" v-show="isSee" class="answerAnalysis">
			<view class="title">答案解析</view>
			<view class="content" v-html="data.analysis"></view>
		</view>
		<!-- 视频 -->
		<view v-if="data.video" v-show="isSee" class="answerAnalysis">
			<view class="title">教你拆解法的短视频</view>
			<video :src="data.video" id="videos" class="videos" controls @click="closeSpeedList" object-fit="cover">
				<cover-view class="video-control">
					<cover-view class="multi rate" @tap="showSwitchRate">X {{currentRate}}</cover-view>
				</cover-view>
				<cover-view class="multi-list rate" :class="{ active: rateShow }">
					<cover-view
						v-for="(item, index) in ['0.5', '1.0', '1.5', '2.0']"
						:key="index"
						class="multi-item rate"
						:data-rate="item"
						@tap="switchRate"
						:class="{ active: item == currentRate }"
					>
						{{ item }}
					</cover-view>
				</cover-view>
			</video>
		</view>
		<!-- 下一步 -->
		<button v-if="data.is_end=='false'" @click="toNext">下一步</button>
		<button v-else @click="toSummary">完成</button>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { subject, checkAnswer } from '@/common/http.api.js';
	export default{
		data() {
			return {
				rateShow: false, // 倍速浮层
				currentRate: 1.0, //默认1倍速
				isSee:false, //解析显示状态
				type:'', //题目类型
				orders:'', //subject_order
				answer:'', //单选答案
				answerList:[], //多选答案
				result:[], //填空答案
				text:'', //翻译答案
				difficulty:'', //难度
				data:{
					answer:'leaders and the'
				}
			}
		},
		onLoad() {
			let order = store.state.order
			console.log('order:'+order)
			uni.getStorage({
				key: 'difficulty',
				success:(res)=> {
					this.difficulty = res.data
					console.log(res.data);
				}
			});
			console.log(this.difficulty)
			this.videoContext = uni.createVideoContext('videos')
			this.token = store.state.vuex_token 
			let id = store.state.subject_id //大题ID
			subject({token:this.token,subject_id:id,order}).then(res=>{
				console.log(res)
				if(res.data.data.option_answer){
					this.isSee = true
				}
				this.type = res.data.data.type
				this.data = res.data.data
				this.orders = res.data.data.subject_order
				if(res.data.data.type=='multiple_choice'){
					for(let i in this.data.option){
						this.data.option[i].isChecd = false
					}
				}
				console.log(this.data)
			}).catch(err=>{
				console.log(err)
			})
		},
		onShow() {
			uni.navigateTo({
				delta: 2
			});
		},
		methods: {
			// 关闭倍速列表
			closeSpeedList(){
				console.log(this.videoList)
				if(this.videoList==true){
					this.rateShow = false;
					this.videoList = false;
				}
			},
			// 显示倍速浮层
			showSwitchRate(rate) {
				if(this.videoList==true){
					this.rateShow = false;
					this.videoList = false;
				}
				let that = this;
				console.log('rateShow');
				that.rateShow = true;
				setTimeout(()=>{
					this.videoList = true
				},500)
			},
			// 切换倍速
			switchRate(e) {
				let that = this;
				let rate = Number(e.currentTarget.dataset.rate);
				that.currentRate = rate;
				that.rateShow = false;
				this.videoContext.playbackRate(rate);
				console.log(rate)
			},
			// 单选 选择答案
			confirm(e) {
				console.log(e)
				for(let i in this.data.option){
					this.data.option[i].isChecd = false
				}
				this.data.option[e].isChecd = true
				console.log(this.data.option)
				console.log(this.data.option[e].isChecd)
				this.answer = this.data.option[e].option
			},
			// 多选 选择答案
			confirms(e) {
				console.log(e)
				this.data.option[e].isChecd = true
				console.log(this.data.option)
				console.log(this.data.option[e].isChecd)
				this.answerList.push(this.data.option[e].option)
			},
			// 查看解析
			seeResolution() {
				if(this.type=='single_choice'){
					//单选解析
					if(this.answer){
						this.isSee = true
						let answers = []
						let id = store.state.subject_id //大题ID
						this.data.option.map((item)=>{
							if(item.isChecd==true){
								return answers.push({answer:item.answer, option:item.option, result:true}) 
							}else{
								answers.push({answer:item.answer, option:item.option, result:null}) 
							}
						})
						let answer = JSON.stringify(answers)
						console.log("-----")
						console.log(answers)
						console.log(answer)
						let params = {
							answer: answer,
							answer_type:this.data.type,
							is_end:this.data.is_end,
							option_id:this.data.id,
							subject_id:id,
							token:this.token
						}
						checkAnswer({...params}).then(res=>{
							console.log(res)
							let id = store.state.subject_id //大题ID
							subject({token:this.token,subject_id:id,order:this.orders-1}).then(res=>{
								console.log(res)
								this.data = res.data.data
								this.orders = res.data.data.subject_order
								console.log(this.data)
							}).catch(err=>{
								console.log(err)
							})
						}).catch(err=>{
							console.log(err)
						})
					}else{
						this.$u.toast("先尝试一下吧~")
					}
				}else if(this.type=='multiple_choice'){
					//多选解析
					if(this.answerList.length!=0){
						this.isSee = true
						let answers = []
						let id = store.state.subject_id //大题ID
						this.data.option.map((item)=>{
							if(item.isChecd==true){
								return answers.push({answer:item.answer, option:item.option, result:true}) 
							}else{
								answers.push({answer:item.answer, option:item.option, result:null}) 
							}
						})
						let answer = JSON.stringify(answers)
						console.log("-----")
						console.log(answers)
						console.log(answer)
						let params = {
							answer: answer,
							answer_type:this.data.type,
							is_end:this.data.is_end,
							option_id:this.data.id,
							subject_id:id,
							token:this.token
						}
						checkAnswer({...params}).then(res=>{
							console.log(res)
							let id = store.state.subject_id //大题ID
							subject({token:this.token,subject_id:id,order:this.orders-1}).then(res=>{
								console.log(res)
								this.data = res.data.data
								this.orders = res.data.data.subject_order
								console.log(this.data)
							}).catch(err=>{
								console.log(err)
							})
						}).catch(err=>{
							console.log(err)
						})
					}else{
						this.$u.toast("先尝试一下吧~")
					}
				}else if(this.type=='completion'){
					//填空解析
					if(this.result.length==this.data.option.length){
						this.isSee = true
						let answers = []
						let id = store.state.subject_id //大题ID
						this.data.option.map((item,index)=>{
								answers.push({key:item.key, value:item.value, answer:this.result[index]}) 
							})
						let answer = JSON.stringify(answers)
						console.log("-----")
						console.log(answers)
						console.log(answer)
						let params = {
							answer: answer,
							answer_type:this.data.type,
							is_end:this.data.is_end,
							option_id:this.data.id,
							subject_id:id,
							token:this.token
						}
						console.log(params)
						checkAnswer({...params}).then(res=>{
							console.log(res)
							let id = store.state.subject_id //大题ID
							subject({token:this.token,subject_id:id,order:this.orders-1}).then(res=>{
								console.log(res)
								this.data = res.data.data
								this.orders = res.data.data.subject_order
								console.log(this.data)
							}).catch(err=>{
								console.log(err)
							})
						}).catch(err=>{
							console.log(err)
						})
					}else{
						this.$u.toast("先尝试一下吧~")
					}
				}else if(this.type=='translate'){
					//翻译解析
					if(this.text){
						this.isSee = true
						let answers = {
							answer:this.text
						}
						let id = store.state.subject_id //大题ID
						let answer = JSON.stringify(answers)
						console.log("-----")
						console.log(answers)
						console.log(answer)
						let params = {
							answer: answer,
							answer_type:this.data.type,
							is_end:this.data.is_end,
							option_id:this.data.id,
							subject_id:id,
							token:this.token
						}
						console.log(params)
						checkAnswer({...params}).then(res=>{
							console.log(res)
						}).catch(err=>{
							console.log(err)
						})
					}else{
						this.$u.toast("先尝试一下吧~")
					}
				}
				
				
				
				
				
			},
			// 下一步
			toNext() {
				if(this.isSee==true){
					this.answer = ''
					this.answerList = []
					this.result = []
					this.text = ''
					let id = store.state.subject_id //大题ID
					this.isSee = false
					subject({token:this.token,subject_id:id,order:this.orders}).then(res=>{
						console.log(res)
						if(res.data.data.option_answer){
							this.isSee = true
						}
						this.type = res.data.data.type
						this.data = res.data.data
						this.orders = res.data.data.subject_order
						console.log(this.data)
					}).catch(err=>{
						console.log(err)
					})
				}else{
					this.$u.toast("先看看解析吧~")
				}
			},
			// 去总结
			toSummary() {
				if(this.isSee==true){
					uni.navigateTo({
						url: '/pages/index/summary'
					})
				}else{
					this.$u.toast("做完再走~")
				}
			},
			// 去单词本
			toNewWord() {
				uni.navigateTo({
					url: '/pages/index/newWordBook'
				})
			},
			// 预览图片
			openImg() {
				uni.previewImage({
					urls:[this.data.img],
				})
			}
		}
	}
</script>

<style lang="scss">
	.body{
		padding-bottom: 40rpx;
		button{
			background: linear-gradient(to right, #ffd23f, #fca51d);
			color: #fff;
			font-size: 32rpx;
			line-height: 88rpx;
			border-radius: 100rpx;
			width: 80%;
		}
		.picture{
			width: 90%;
			margin: 0 5%;
			height: 200rpx;
		}
		// 单选类型
		.single_choice{
			.answerList{
				margin: 40rpx 30rpx;
				.answer{
					margin: 30rpx 0;
					display: flex;
					.serial{
						margin-right: 20rpx;
						border: 1px solid #cbcbcb;
						border-radius: 50%;
						color: #cbcbcb;
						width: 70rpx;
						height: 70rpx;
						line-height: 70rpx;
						text-align: center;
					}
					.checked{
						border: 1px solid #50d891;
						color: #50d891;
					}
					.trueAnswer{
						border: 1px solid #50d891;
						background-color: #50d891;
						color: #fff;
					}
					.falseAnswer{
						border: 1px solid #ff6227;
						background-color: #ff6227;
						color: #fff;
					}
					.text{
						line-height: 70rpx;
						flex: 6;
					}
				}
				button{
					padding: 0;
					color: #ffd942;
					width: 150rpx;
					border: 1px solid #ffd942;
					border-radius: 12rpx;
					font-size: 28rpx;
					line-height: 60rpx;
					background-color: transparent;
					background: none;
					margin-bottom: 40rpx;
				}
			}
		}
		// 多选类型
		.multiple_choice{
			.answerList{
				margin: 40rpx 30rpx;
				.answer{
					margin: 30rpx 0;
					display: flex;
					.serial{
						margin-right: 20rpx;
						border: 1px solid #cbcbcb;
						border-radius: 50%;
						color: #cbcbcb;
						width: 70rpx;
						height: 70rpx;
						line-height: 70rpx;
						text-align: center;
					}
					.checked{
						border: 1px solid #50d891;
						color: #50d891;
					}
